<template>
  <div class="login-container">
    <div class="mian-cont">
      <div class="login-tab">

        <div class="txt"></div>

        <div class="form">
          <input class="input-all" type="text" placeholder="请输入工号" v-model="workNum"/>
          <input class="input-all" type="password" placeholder="请输入密码" v-model="workPassword"/>
          <input class="input-all" type="text" placeholder="请输入验证码"/>
        </div>

        <span class="error-info">{{errorInfo}}</span>

        <div class="botton" @click="handleLogin">立即登录</div>

        <div class="more-info">
          <el-checkbox v-model="checked">记住用户名</el-checkbox>
          <span class="forget" @click="forgetPsd = true">忘记密码</span>
        </div>

      </div>

    </div>

    <div class="layer-cont" v-if="tipFlag">
      <div class="layer"></div>
      <div class="more-tip">
        <span class="tip-pic"></span>
        <span class="tip-info">忘记密码请联系管理员！！！</span>
        <div class="tip-button">好的</div>
      </div>
    </div>

  </div>

</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      checked: false,  // true false
      errorInfo: '',
      radio: '1',
      tipFlag: false,
      workNum: 'admin',
      workPassword: '111111',
      forgetPsd: false,
      labelPosition: 'left',
      formLabelAlign: {
        name: '',
        region: '',
        type: ''
      }
    }
  },
  mounted () {
  },
  methods: {
    handleLogin () {
      let self = this
      this.$store.dispatch('LoginByUsername', {
        username: 'admin',
        password: '1111111'
      }).then(() => {
        self.$router.push({ path: '/infoQuery' })
      }).catch(() => {
        // TODO
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss">

  .login-container {
    width: 100%;
    height: 100vh;
    background: $webColor;
    display: flex;
    justify-content: center;
    align-items: center;

    .mian-cont{
      display: flex;
      justify-content: center;
      align-items: center;

      width: 1210px;
      height: 650px;
      background: #FFFFFF;
      border-radius: 5px;
      box-shadow: 0 2px 11px 0 rgba(0,0,0,0.48);

      .login-tab{
        display: flex;
        flex-direction: column;
        align-items: center;

        width: 410px;
        height: 100%;

        .txt{
          margin-top: 92px;
          @include getIcon("../../assets/image/login-txt.png",72px,24px);
        }
        .form{
          margin-top: 60px;
          width: 286px;
          border-top:1px solid #D9D9D9;
          border-left:1px solid #D9D9D9;
          border-right:1px solid #D9D9D9;
          border-radius:5px;
          .input-all{
            width: 100%;
            height: 40px;
            border-bottom:1px solid #D9D9D9;
            padding-left:10px;
          }
        }

        .error-info{
          margin-top: 11px;
          width: 286px;
          color: #F06161;
          font-size: 12px;
          height: 18px;
          line-height: 18px;
        }

        .botton{
          margin-top: 11px;
          width: 286px;
          height: 40px;
          background: #FF6600;
          box-shadow: 0 2px 11px 0 rgba(255,98,0,0.48);
          border-radius: 3px;

          color: #FFFFFF;
          font-size: 14px;
          text-align: center;
          line-height: 40px;
        }

        .more-info{
          margin-top: 40px;
          width: 286px;
          height: 18px;
          font-size: 14px;
          line-height: 18px;
          color: #666666;
          display: flex;
          justify-content: space-between;
        }
      }

    }


    /************* input placeholder样式 ************/
    :-moz-placeholder {
      font-size: 14px;
      color: #ccc;
    }

    ::-moz-placeholder {
      font-size: 14px;
      color: #ccc;
    }

    input:-ms-input-placeholder{
      font-size: 14px;
      color: #ccc;
    }

    input::-webkit-input-placeholder{
      font-size: 14px;
      color: #ccc;
    }
    /************* input placeholder样式 ************/

    .layer-cont{
      position: absolute;
      top: 0;
      left: 0;
      z-index: 500;
      width: 100%;
      height: 100vh;
      display: flex;
      justify-content: center;
      align-items: center;
      .layer{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.5;
        background: #000000;
      }
      .more-tip{
        z-index: 501;
        width: 240px;
        height: 146px;
        background: #FFFFFF;
        border-radius: 5px;

        font-size: 14px;
        color: #4A4A4A;

        display: flex;
        align-items: center;
        flex-direction: column;


        .tip-info{
          margin-top: 52px;
        }
        .tip-button{
          margin-top: 22px;
          width: 85px;
          height: 30px;

          background: #FF6600;
          box-shadow: 0 2px 11px 0 rgba(255,98,0,0.48);
          border-radius: 3px;

          color: #FFFFFF;
          font-size: 14px;
          text-align: center;
          line-height: 30px;
        }
      }
    }

    /************* 密码修改dialog样式 ************/
    .layer-psd {
      .el-dialog {
        height: 100vh;
      }

      .psd-icon {
        width: 44px;
        height: 44px;
        display: block;
        background-image: url("../../assets/svg/icon-lock.svg");
        margin: 0 auto;
        margin-top: 150px;
      }
      .psd-tip {
        margin-top: 19px;
        font-size: 16px;
        color: #4A4A4A;
        text-align: center;
        width: 100%;
        font-weight: bold;
      }
      .psd-form {
        margin-top: 56px;
        margin-left: 162px;
        .el-input {
          width: 322px;
        }
        .el-button {
          width: 322px;
          margin-left: 100px;
          background-color: $violetColor;
          font-size: 14px;
          color: #FFFFFF;
        }
      }
    }

  }

</style>



